<template>
    <div>
        <!-- 降雨量 -->
        <div class="item-pr" v-for="(v,i) in hlist">
            <h4>
                <span>{{i}}</span>
                <strong>{{v.name}}</strong>
                <b @click="showIdFn(v,i)">更多</b>
            </h4>
            <ul v-show="showId==i" v-cloak v-for="(sv,si) in subhlist">
                <li class="li-title">
                    {{sv.position}}
                </li>
                <li>
                    {{deviceParam[sv.param_code1]}}
                    <b>{{sv.param_value1}}{{sv.param_unit1}}</b>
                </li>
                <li v-show="sv.param_num>=2" v-cloak>
                    {{deviceParam[sv.param_code2]}}
                    <b>{{sv.param_value2}}{{sv.param_unit2}}</b>
                </li>
                <li v-if="sv.param_num>=3" v-cloak>
                    {{deviceParam[sv.param_code3]}}
                    <b>{{sv.param_value3}}{{sv.param_unit3}}</b>
                </li>
                <li v-show="sv.param_num>=4" v-cloak>
                    {{deviceParam[sv.param_code4]}}
                    <b>{{sv.param_value4}}{{sv.param_unit4}}</b>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import { Toast } from "vant";
export default {
    data() {
        return {
            showId: -1,
            subhlist: [],
            deviceParam:{}
        };
    },
    props: ["hlist"],
    methods: {
        //显示
        showIdFn(v, i) {
            console.log("详情");
            let self = this;
            self.showId == i ? (self.showId = -1) : (self.showId = i);
            //
            let data = {
                projectId: HGetHashUrl("projectId"),
                code: v.code
            };
            self.$.Get("/project/monitorItemDetail", data, re => {
                console.log(re);
                self.subhlist = re.info.list;
                self.deviceParam=re.info.deviceParam;
                // console.log(deviceParam);
                if (!self.subhlist || self.subhlist.length == 0) {
                    Toast("暂无数据");
                }
            });
        }
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.item-pr {
    border-radius: rem(10);
    background-color: white;
    font-size: rem(40);
    box-shadow: rem(2) rem(5) rem(20) rem(2) #d4d4d4;
    margin-bottom: rem(30);
    h4 {
        height: rem(95);
        line-height: rem(95);
        padding: 0 rem(30);
        > span {
            background-color: #cecece;
            padding: rem(1) rem(10);
            color: white;
            height: rem(40);
        }
        strong {
            font-weight: bold;
        }
        b {
            color: $color;
            float: right;
        }
    }
    ul {
        padding-left: rem(50);
        .li-title {
            //  color: #ccc;
        }
        .li-title::before {
            display: none;
        }
        li {
            padding: 0 rem(30);
            display: block;
            border-top: 1px solid $bordercolor;
            height: rem(95);
            line-height: rem(95);
            text-indent: rem(10);
            margin-bottom: rem(35);
            span {
                color: #8d8d8d;
            }
            b {
                float: right;
                color: $color;
            }
        }
        li::before {
            content: "";
            display: block;
            width: rem(10);
            height: rem(10);
            border-radius: 50%;
            background-color: #8d8d8d;
            line-height: rem(95);
            float: left;
            margin-top: rem(42);
        }
    }
}
</style>



