<template>
    <div class="monitoring">
        <hheader :header="hheader">
            <i class="iconfont icon-ren" slot="hleft"></i>
        </hheader>
        <section class="container has-header has-gray has-footer">
            <div class="swipe">
                <van-swipe :autoplay="3000">
                    <van-swipe-item v-for="(image, index) in images" :key="index" @change="onChange">
                        <img :src="image.imgUrl" />
                    </van-swipe-item>
                </van-swipe>
                <div class="swipe-footer">
                    <div class="swipe-footer-back"></div>
                    <p>
                        {{swipeMsg}}
                    </p>
                </div>
            </div>
            <div class="two">
                <h4><img src="../../static/img/整体健康状况图标.png" alt=""> 整体健康状况</h4>
                <ul class="two-ul">
                    <li>
                        <i class="iconfont icon-iconset0457"></i>
                        <p>环境类</p>
                        <van-button type="default" size="mini" v-if="status.hjl==1">正常</van-button>
                        <van-button type="danger" size="mini" v-else>报警</van-button>
                    </li>
                    <li>
                        <i class="iconfont icon-icon--"></i>
                        <p>变形类</p>
                        <van-button type="default" size="mini" v-if="status.bxl==1">正常</van-button>
                        <van-button type="danger" size="mini" v-else>报警</van-button>
                    </li>
                    <li>
                        <i class="iconfont icon-duocengjiegou"></i>
                        <p>结构类</p>
                        <van-button type="default" size="mini" v-if="status.jgl==1">正常</van-button>
                        <van-button type="danger" size="mini" v-else>报警</van-button>
                    </li>
                </ul>
            </div>
            <div class="main">
                <hlist :hlist="hlist"></hlist>
            </div>
        </section>
        <hfooter></hfooter>
    </div>
</template>

<script>
import hlist from "../components/list.vue";
export default {
    data() {
        return {
            hheader: {
                title: "基坑监测项目"
            },
            hlist: [],
            status: [],
            swipeMsg: "",
            images: [
                {
                    imgUrl: "../../static/img/111.png",
                    msg: ""
                },
                {
                    imgUrl: "../../static/img/111.png",
                    msg: ""
                },
                {
                    imgUrl: "../../static/img/111.png",
                    msg: ""
                }
            ]
        };
    },
    created() {
        this.getStatus(); //获取状态
        this.getData(); //获取数据 第一层标题
    },
    methods: {
        //轮播 改变
        onChange(i) {
            this.swipeMsg = this.images[i];
        },
        //获取状态
        getStatus() {
            let self = this;
            self.$.Post("/project/sdlStatus", {}, re => {
                self.status = re.info;
            });
        },
        //获取banner图
        getBanner() {
            let self = this;
            let data={
                projectId:HGetHashUrl("projectId"),
                code:"cjjc"
            }
            self.$.Post("/project/monitorItemDetail", data, re => {
                console.log(re);
            });
        },
        //获取数据 第一层标题
        getData() {
            let self = this;
            self.$.Post("/project/sdlMonitorItem", {}, re => {
                self.hlist=re.info;
            });
        }
    },
    components: {
        hlist
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.monitoring {
    .main {
        margin: rem(20);
    }
    .swipe {
        position: relative;
        .swipe-footer {
            position: absolute;
            bottom: rem(105);
            width: 100%;
            color: white;
            > p {
                position: absolute;
                color: white;
                z-index: 1000000;
                height: rem(105);
                line-height: rem(105);
                width: 100%;
                font-size: rem(60);
                text-indent: rem(20);
                top: 0;
            }
            .swipe-footer-back {
                top: 0;
                position: absolute;
                z-index: 800;
                background-color: #000;
                opacity: 0.5;
                height: rem(105);
                width: 100%;
            }
        }
        .van-swipe__indicators {
            right: rem(60);
            left: 90%;
        }
    }
    .van-swipe {
        height: rem(773);
        img{
            height: 100%;
            width:100%;
        }
    }
    .two {
        font-size: rem(45);
        margin: rem(20);
        padding: rem(20);
        overflow: hidden;
        background-color: white;
        > h4 {
            border-bottom: 1px solid #d4d4d4;
            padding: rem(20) 0;
            margin-bottom: rem(20);
            img {
                height: rem(40);
                width: auto;
                margin: 0 rem(10);
            }
        }
        .two-ul {
            overflow: hidden;
            margin-right: rem(-60);
            li {
                width: rem(307);
                height: rem(293);
                position: relative;
                color: white;
                text-align: center;
                float: left;
                margin-left: rem(60);
                i {
                    display: inline-block;
                    font-size: rem(62);
                    margin-top: rem(35);
                }
                p {
                    margin: rem(30) 0;
                }
                .van-button {
                    border: 0;
                    border-radius: rem(30);
                }
                .van-button--danger {
                    color: white !important;
                }
            }
            li:nth-child(1) {
                background-color: #28c189;
                .van-button {
                    color: #28c189;
                }
            }
            li:nth-child(2) {
                background-color: #b967ff;
                .van-button {
                    color: #b967ff;
                }
            }
            li:nth-child(3) {
                background-color: #ffb75a;
                .van-button {
                    color: #ffb75a;
                }
            }
            .on {
                background-color: #ffb75a;
            }
        }
    }
}
</style>



